import vue, {lib, library} from "vue.min.js"
import ini from "/src/package.json"
let {define} = lib
let {zero, one} = lib

vue.setting = function (key, value) {
	if (arguments.length > one) return vue.setting.data [key] = value
	else if (key) return vue.setting.data [key]
	else return vue.setting.data
	}

vue.setting.data = vue.reactive ()

vue.setting ("media-query:screen", lib.storage.set ("media-query:screen", null, true))
vue.setting ("media-query:line", lib.storage.set ("media-query:line", null, true))
vue.setting ("media-query:audio", lib.storage.set ("media-query:audio", null, true))
vue.on ("web-socket", function () {
	if ("media-query:screen" in vue.package.setting) if ((vue.package.setting ["media-query:screen"] === lib.storage.get ("media-query:screen")) === false) vue.setting ("media-query:screen", lib.storage.set ("media-query:screen", vue.package.setting ["media-query:line"]))
	if ("media-query:line" in vue.package.setting) if ((vue.package.setting ["media-query:line"] === lib.storage.get ("media-query:line")) === false) vue.setting ("media-query:line", lib.storage.set ("media-query:line", vue.package.setting ["media-query:line"]))
	if ("media-query:audio" in vue.package.setting) if ((vue.package.setting ["media-query:audio"] === lib.storage.get ("media-query:audio")) === false) vue.setting ("media-query:audio", lib.storage.set ("media-query:audio", vue.package.setting ["media-query:line"]))
	})
